/* purgecss start ignore */

.lc-input-select .MuiAutocomplete-inputRoot[class*='MuiOutlinedInput-root'] {
    padding: 4px;
  }

.lc-input-select .MuiAutocomplete-inputRoot[class*='MuiOutlinedInput-root'] .MuiAutocomplete-input {
      height: 38px;
      padding: 12px 0 12px 4px;
    }

.lc-input-select .MuiAutocomplete-inputRoot[class*='MuiOutlinedInput-root'] .MuiAutocomplete-input:first-child {
        padding-left: 12px;
      }

.lc-input-select .MuiAutocomplete-endAdornment {
    top: calc(50% - 15px);
  }

.lc-input-select .MuiInputLabel-outlined {
    top: -1px;
    transform: translate(14px, 17px);
  }

.lc-input-select .MuiAutocomplete-tag.MuiChip-root {
    --tw-bg-opacity: 1;
    background-color: rgba(var(--lc-color-gray-lighter), var(--tw-bg-opacity));
    font-family: var(--lc-font-family-sans);
    height: 32px;
    font-size: 12px;
    border-radius: 4px;
  }

.lc-input-select .MuiAutocomplete-tag.MuiChip-root .MuiChip-label {
      padding-left: 12px;
      padding-right: 12px;
    }

.lc-input-select .MuiAutocomplete-tag.MuiChip-root .MuiChip-deleteIcon {
      --tw-text-opacity: 1;
      color: rgba(var(--lc-color-gray-dark), var(--tw-text-opacity));
      width: 12px;
      height: 12px;
      margin-left: -4px;
      margin-right: 12px;
    }

.lc-input-select [class*='lc-input-select-icon'] {
    color: var(--gray);
  }

body .MuiAutocomplete-paper {
  font-size: inherit;
  font-family: inherit;
}

.ReactModal__Body--open .MuiAutocomplete-popper {
  z-index: 10000;
}

.lc-input-label-above .MuiAutocomplete-inputRoot[class*='MuiOutlinedInput-root'] {
    background: white;
  }

/* purgecss end ignore */
